* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.left-aligned{
  margin-left: 15%;
}
.right-aligned{
  margin-right: 25%;
}
.big-margin-top {
  margin-top: 50px;
  margin-bottom: 15px;
}
.small-margin-top{
  margin-top: 15px;
}
.big-margin-left{
  margin-left: 50px;
}
.all-margins {
  margin: 15px;
}
.all-small-margins {
  margin: 10px;
}
.top-bottom-margins {
  margin-top: 10px;
  margin-bottom: 10px;
}
.medium-top-bottom-margins {
  margin-top: 30px;
  margin-bottom: 30px;
}
.big {
  width: 375px;
}
.small {
  width: 250px;
}
.full-width{
  width: 100%;
}
.lower-border{
  border-bottom: 1px solid gray;
}
.right-corner {
  margin-left: auto;
}
.bg-gray {
  background-color: gainsboro;
}
.small-square {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.medium-square {
  width: 70px;
  height: 70px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; 
  font-size: 1em;
}
.big-text {
  font-family: 'Bahnschrift Light';
  font-size: 1.5em;
}
.small-text {
  font-family: 'Bahnschrift Light';
  font-size: 0.75em;
}
.center-text {
  text-align: center;
}
.page-header{
  display: flex;
  align-items: center;
  flex-direction: row;
  width: "100%";
  height: 60px;
  margin-top: 0px;
}
.background-picture{
  width: 100%;
  height: calc(100vh - 60px);
  display: flex;
  align-items: baseline;
  flex-direction: column;
  /*background-image: url("./bg-pic.jpg");*/
  background-size: cover;
  background-position: center;
  animation: swap-image 13s infinite normal;
}
@keyframes swap-image {
  0% {
      background-image: url("./bg-pic.jpg");
  }
  50% {
      background-image: url("./bg-pic-2.jpg");
  }
  100% {
      background-image: url("./bg-pic.jpg");
  }
}
.logo{
  position: relative;
  font-family: 'Times New Roman';
  font-size: 1.5em;
}
.logo:before{
  content:'';
  position: absolute;
  width: 100%;
  height: 8px;
  z-index: -1;
  background-color: goldenrod;
  opacity: .5;
  top: 18px
}
.menu{
  margin-left: auto;
  margin-right: 30px;
  display: flex;
  align-items: baseline;
  flex-direction: row;
  cursor: pointer;
}
.menu-label{
  margin: 0px 20px 0px 20px;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.article-brand{
  text-align: left;
  margin-bottom: 10;
  margin-top: auto;
  color: white;
  font-family: 'Bahnschrift';
  font-size: 1em;
}
.article-title{
  margin-bottom: 10;
  color: white;
  /* font: 6em "Times New Roman"; */
  font-family: 'Bahnschrift Light';
  font-size: 4em;
  padding: 0;
}

.article-authors{
  margin-bottom: 50px;
  justify-content: flex-end;
  color: white;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.golden {
  color: goldenrod;
}
.gray {
  color: gray;
}
.black {
  color: black;
}
.label{
  margin-right: 50px;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.main-container{
  margin-top: 40px;
  margin-left: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  flex-direction: row;
}
.grid {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.grid-row {
  display: flex;
  /* align-items: flex-start;
  justify-content: flex-start;
  flex-direction: row; */
}
.grid-row-center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.shadowed {
  box-shadow: 0 0 5px 1px gainsboro;
}
.dark-shadowed {
  box-shadow: 0 0 5px 1px rgb(56, 56, 56);
}
.bordered {
  border: 1px solid silver;
}
.grid-cell {
  background-color: white;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.grid-cell-picture{
  width: 100%;
  height: auto;
}
.cell-label{
  margin: 15px 20px 5px 20px;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.cell-title{
  margin: 0px 20px 0px 20px;
  text-align: center;
  font-family: 'Bahnschrift Light';
  font-size: 1.3em;
}
.cell-text{
  margin: 10px 10px 15px 10px;
  text-align: center;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.cell-additional-text{
  margin: 5px 15px 10px 10px;
  text-align: center;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.cell-huge-text{
  margin: 10px 15px 10px 10px;
  text-align: center;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.button-text{
  margin: 10px 10px 10px 10px;
  text-align: center;
  font-family: 'Bahnschrift Light';
  font-size: 1.5em;
}
.side-menu{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: 40px;
}
.usual-text {
  text-align: center;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.big-text {
  text-align: center;
  font-family: 'Bahnschrift Light';
  font-size: 1.3em;
}
.author-card{
  margin: 15px 15px 0px 15px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.inner-grid-cell{
  border-top: 1px solid gainsboro;
  /* border-left: 1px solid gray;
  border-bottom: 1px solid gray;
  border-right: 1px solid gray; */
  display: flex;
  align-items: center;
  flex-direction: column;
}
.insta-picture{
  width: 14vw;
  height: 14vw;
  margin: 1vw;
}
.insta-picture:hover{
  transform: scale(1.05);
  transition: all ease 500ms;
}
.pagination-text {
  font-family: 'Bahnschrift Light';
  margin: 10px 15px 10px 15px;
}
.animated {
  cursor: pointer;
  color: black;
  -webkit-transition: color 0.5s;
  -moz-transition:    color 0.5s;
  -ms-transition:     color 0.5s;
  -o-transition:      color 0.5s;
  transition:         color 0.5s;
}
.animated:hover {
  color: goldenrod;
}
.modal-background{
  background-image: url("cell-1.jpg");
}
.dark-banner {
  background-color: rgb(0,0,0,0.4); 
  width: 100%; 
  height: 100%; 
  position:fixed; 
  top: 0; 
  bottom: 0; 
  display: flex; 
  justify-content: center; 
  align-items: center; 
  flex-direction: column;
  animation: fadeIn 1s;
      -webkit-animation: fadeIn 1s;
      -moz-animation: fadeIn 1s;
      -o-animation: fadeIn 1s;
      -ms-animation: fadeIn 1s;
}
@keyframes fadeIn {
  0% { background-color: rgb(0,0,0,0) }
  100% { background-color: rgb(0,0,0,0.4); }
}
@-moz-keyframes fadeIn {
0% { background-color: rgb(0,0,0,0) }
100% { background-color: rgb(0,0,0,0.4); }
}
@-webkit-keyframes fadeIn {
0% { background-color: rgb(0,0,0,0) }
100% { background-color: rgb(0,0,0,0.4); }
}
@-o-keyframes fadeIn {
0% { background-color: rgb(0,0,0,0) }
100% { background-color: rgb(0,0,0,0.4); }
}
@-ms-keyframes fadeIn {
0% { background-color: rgb(0,0,0,0) }
100% { background-color: rgb(0,0,0,0.4); }
}